.portfolio {
    text-align: center;
    height: 100%; 
    color: #343a40;
}

.portfolio-background-img {
    height: 90vh;
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
} 

.profile-header-text { 
    text-align: center;
    z-index: 1;
    margin-top: -400px;
    margin-bottom: 300px;
} 

.header-text h1 {
    font-size: 50px;
} 

/* about section */

.profile-about {
    height: 100%;
    width: 100%;
    background-color:  #cfcdc6;
}

.profile-about-header {
    padding-top: 135px;
    padding-bottom: 60px;
} 

.profile-about-text-section { 
    background-color: #cfcdc6 ;
    display: flex;
    align-items: left;
    padding-bottom: 100px;
} 

.profile-about-text {
    width: 70%;
    display: block;
    text-align: left;
    padding: 5%;
    border-right: 2px solid #6c757d ;
    padding-bottom: 50px;
} 
 
.profile-about-text h3 {
    padding-bottom: 10px;
}
.profile-about-detail { 
    width: 30vw;
    justify-content: center;
   
} 

.profile-about-contact {
    width: 100%;
    padding-top: 50px;
    
} 

